import {DIRECTORIES_FETCH} from '../actions/DirectoriesAction';

let inititalState = [
        {
            "id": 1,
            "name": " ",
            "icon": "iconTh"
        },
        {
            "id": 2,
            "name": " ",
            "icon": "iconCogs"
        },
        {
            "id": 3,
            "name": " ",
            "icon": "iconThumbsUp"
        },
        {
            "id": 4,
            "name": " ",
            "icon": "iconBook"
        },
        {
            "id": 5,
            "name": "",
            "icon": "iconComment"
        },
        {
            "id": 6,
            "name": "  ",
            "icon": "iconCogs"
        },
        {
            "id": 7,
            "name": " ",
            "icon": "iconCreditCard"
        },
        {
            "id": 8,
            "name": " ",
            "icon": "iconBarChart"
        },
        {
            "id": 9,
            "name": "  ",
            "icon": "iconInbox"
        }
];


const direcotires = (state = inititalState, action ) => {

    switch (action.type){
        case DIRECTORIES_FETCH:
            return [...state];
        default:
            return state
    }
};

export default direcotires;



